import {Component, ViewChild} from '@angular/core';
import {
  IonicPage,
  NavController,
  NavParams,
  AlertController,
  ToastController,
  Refresher,
  Content,
  ModalController,
  Searchbar
} from 'ionic-angular';
import {QuestionProvider} from "../../providers/question/question";
import {AnswersPage} from "../answers/answers";


/**
 * Generated class for the FaqPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-faq',
  templateUrl: 'faq.html',
})
export class FaqPage {

  @ViewChild(Content) content: Content;
  @ViewChild(Refresher) refresher: Refresher;
  @ViewChild(Searchbar) searchbar: Searchbar;

  FAQs: any = [];
  temp: any = [];
  keyword = '';

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public alertCtrl: AlertController,
              public questionProvider: QuestionProvider,
              public toastController: ToastController,
              public modalController: ModalController) {


    this.questionProvider.queryFAQs()
      .forEach(next => {
        next.forEach(doc => {
          let questionData = doc.data();
          questionData["id"] = doc.id;
          this.FAQs.push(questionData);
          this.temp.push(questionData);
        })
      });


  }

  onInput(e){
    if (this.searchbar.value === ''){
      this.temp = [...this.FAQs];
      return
    }
    this.temp = [];
    this.FAQs.forEach(f => {
      if (f.title.toLowerCase().search(this.searchbar.value) !== -1 || f.question.toLowerCase().search(this.searchbar.value) !== -1){
        this.temp.push(f);
      }
    })
  }
  onCancel(e){
    this.keyword = '';
    this.temp = [...this.FAQs];
  }

  async presentModal(FAQ) {
    const modal = await this.modalController.create(AnswersPage,{
      id: FAQ.id,
      title: FAQ.title,
      question: FAQ.question,
      answers: FAQ.answers,
      addAnswer: this.addAnswer
    });
    return await modal.present();
  }




  ionViewDidEnter() {
    this.refresher._top = '110px';
    this.refresher.state = 'refreshing';
    this.refresher._beginRefresh();
  }

  doRefresh(event) {
    setTimeout(() => {
      event.complete();
    }, 2000);
  }

  onSuccessToAddFaq = async () => {
    const toast = await this.toastController.create({
      message: 'Added Successfully',
      duration: 1500
    });
    toast.present();
  };

  onSuccessToEditFaq = async () => {
    const toast = await this.toastController.create({
      message: 'Edited Successfully',
      duration: 2000
    });
    toast.present();
  };

  onSuccessToDeleteFaq = async () => {
    const toast = await this.toastController.create({
      message: 'Deleted Successfully',
      duration: 2000
    });
    toast.present();
  };

  onFailToAddFaq = async () => {
    const toast = await this.toastController.create({
      message: 'Failed. Please try again.',
      duration: 2000
    });
    toast.present();
  };


  addFAQ(){
    let prompt = this.alertCtrl.create({
      title: 'Ask a question',
      inputs: [{ name: 'title', placeholder: 'Title'},
        {name: 'question', placeholder: 'Type your question'}],
      buttons: [{text: 'Cancel'},
        {text: 'Add', handler: data => {
          data.answers = [];
          let [callback, newData] = this.questionProvider.addFAQ(data);
          callback.then(this.onSuccessToAddFaq)
            .catch(this.onFailToAddFaq);
          this.FAQs.push(newData);
          this.temp = [...this.FAQs];

        }
        }
      ]
    });
    prompt.present();
    //console.log(this.FAQs);
  }

  editFAQ(FAQ){
    let prompt = this.alertCtrl.create({
      title: 'Edit this question',
      inputs: [{ name: 'title', placeholder: FAQ.title},
        {name: 'question', placeholder: FAQ.question}],
      buttons: [{text: 'Cancel'},
        {text: 'Save',
          handler: data => {
            let index = this.FAQs.indexOf(FAQ);
            data.id = FAQ.id;
            data.answers = FAQ.answers;
            if(index > -1){this.FAQs[index] = data;
            this.questionProvider.editFAQ(data.id, data.title || FAQ.title, data.question || FAQ.question)
              .then(this.onSuccessToEditFaq)
              .catch(this.onFailToAddFaq);
              this.temp = [...this.FAQs];
            }
          }
        }]
    });
    prompt.present();
  }

  deleteFAQ(FAQ){
    let index = this.FAQs.indexOf(FAQ);
    this.FAQs = [...this.FAQs.slice(0, index), ...this.FAQs.slice(index+1, this.FAQs.length)]
    this.questionProvider.deleteFAQ(FAQ.id)
      .then(this.onSuccessToDeleteFaq)
      .catch(this.onFailToAddFaq)
    this.temp = [...this.FAQs];
  }

  addAnswer = (id, answers) => {
    return this.questionProvider.addAnswer(id, answers);
  }
}



