import {Component} from '@angular/core';
import {IonicPage, NavParams, ToastController, ViewController} from 'ionic-angular';
import { FormBuilder, FormGroup } from '@angular/forms';
/**
 * Generated class for the AnswersPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-answers',
  templateUrl: 'answers.html',
})
export class AnswersPage {

  myForm: FormGroup;

  constructor(public navParams: NavParams,
              public modalCtrl: ViewController,
              public builder: FormBuilder,
              public toastController: ToastController,) {
    this.myForm = this.builder.group({
      answer:''
    })

  }

  onSuccessToAddFaq = async () => {
    const toast = await this.toastController.create({
      message: 'Added Successfully',
      duration: 1500
    });
    toast.present();
  };

  onFailToAddFaq = async () => {
    const toast = await this.toastController.create({
      message: 'Failed. Please try again.',
      duration: 2000
    });
    toast.present();
  };


  addAnswer(){
    this.navParams.data.answers.push(this.myForm.value.answer);
    this.myForm = this.builder.group({
      answer:''
    });
    this.navParams.data.addAnswer(this.navParams.data.id, this.navParams.data.answers)
      .then(this.onSuccessToAddFaq)
      .catch(this.onFailToAddFaq);

  }


  dismissModal() {
    this.modalCtrl.dismiss({
      'dismissed': true,
      'newFAQ': this.navParams.data
    });
  }

}



