import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
/*
  Generated class for the QuestionProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class QuestionProvider {

  questionCollectionRef: AngularFirestoreCollection<any>;

  constructor(public http: HttpClient, public firestore: AngularFirestore) {
    this.questionCollectionRef = this.firestore.collection<any>('FAQs');
  }

  addFAQ(data){
    const id = this.firestore.createId();
    let temp = this.questionCollectionRef.doc(id).set(data);
    return [temp, {...data, id}]
  }

  editFAQ(id, title, question){

    return this.questionCollectionRef.doc(id).update({
      title, question
    });
  }

  queryFAQs(){
    return this.questionCollectionRef.get();
  }

  deleteFAQ(id){
    return this.questionCollectionRef.doc(id).delete();
  }

  addAnswer(id, answers){
    return this.questionCollectionRef.doc(id).update({
      answers
    });
  }

}



